
.fade-enter-active {
  -webkit-transition: all 0.3s ease;
  transition: all 0.3s ease;
}
.fade-leave-active {
  -webkit-transition: all 0.3s ease;
  transition: all 0.3s ease;
}
.fade-leave,
.fade-enter-to {
  opacity: 1;
}
.fade-leave-to,
.fade-enter {
  opacity: 0;
}
.router-enter-active {
  -webkit-transition: all 0.3s ease;
  transition: all 0.3s ease;
}
.router-leave-active {
  -webkit-transition: all 0.3s ease;
  transition: all 0.3s ease;
}
.router-enter,
.router-leave-to {
  -webkit-transform: translateX(10px);
          transform: translateX(10px);
  opacity: 0;
}
body {
